.whatsapp_for_wordpress_vi-public a {
  text-decoration: unset;
}
.whatsapp_for_wordpress_vi-public a:focus {
  text-decoration: unset;
}
.whatsapp_for_wordpress_vi-public ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.whatsapp_for_wordpress_vi-public textarea {
  width: 100%;
  resize: none;
}

@keyframes wa_for_wp_vi-effects-pulsing {
  to {
    box-shadow: 0 0 0 10px rgba(232, 76, 61, 0);
  }
}
.just_hide_this {
  display: none;
}

.effects_pulse a {
  width: 60px;
  height: 60px;
  font-size: 30px;
  border-radius: 50px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translatey(0px);
  box-shadow: 0 0 0 0 rgba(37, 211, 102, 0.6901960784);
  animation: wa_for_wp_vi-effects-pulsing 1.25s infinite cubic-bezier(0.44, 0, 0, 0.44);
  font-weight: normal;
  font-family: sans-serif;
  text-decoration: none !important;
  transition: all 300ms ease-in-out;
}

.whatsapp_for_wordpress_vi-public img {
  width: 100%;
  max-width: 100%;
  height: auto;
}
.whatsapp_for_wordpress_vi-public-basic {
  position: fixed;
  bottom: 40px;
  right: 40px;
}
.whatsapp_for_wordpress_vi-public-basepop {
  transition: all 0.7s ease-in-out;
}
.whatsapp_for_wordpress_vi-public-basepop-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
}
.whatsapp_for_wordpress_vi-public-basepop-btn a.activated img {
  filter: grayscale(15%) drop-shadow(1px 1px 4px);
}
.whatsapp_for_wordpress_vi-public-basepop-btn .wa-float-btn {
  display: flex;
}
.whatsapp_for_wordpress_vi-public-basepop-btn .wa-float-btn .whatsapp_for_wordpress_vi-public-basepop-btn-openicon,
.whatsapp_for_wordpress_vi-public-basepop-btn .wa-float-btn span {
  display: none;
  transition: all 1s ease-in-out;
}
.whatsapp_for_wordpress_vi-public-basepop-btn .wa-float-btn.activated {
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 6px 40px;
  border-radius: 20px;
  border: 1px solid;
  text-decoration: none;
  background-color: #eff;
  transition: all 0.3s ease-in-out;
}
.whatsapp_for_wordpress_vi-public-basepop-btn .wa-float-btn.activated .whatsapp_for_wordpress_vi-public-basepop-btn-openicon {
  display: block;
  width: 30px;
}
.whatsapp_for_wordpress_vi-public-basepop-btn .wa-float-btn.activated span {
  display: block;
  font-size: 16px;
  font-family: "Dosis", sans-serif;
}
.whatsapp_for_wordpress_vi-public-basepop-btn .wa-float-btn.activated .whatsapp_for_wordpress_vi-public-basepop-btn-deficon {
  display: none;
}
.whatsapp_for_wordpress_vi-public-basepop-inner {
  position: fixed;
  bottom: 80px;
  right: 5px;
  width: 320px;
  min-height: 350px;
  z-index: 0;
  background: #b0b0b0;
  max-height: 500px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.2);
  border: 1px solid #ccc;
  border-top: none;
  background: rgba(0, 0, 0, 0.5);
  background: #f7f7f7;
  border-radius: 20px;
  box-sizing: border-box;
  z-index: 1000;
  max-width: calc(100% - 10px);
  display: none;
  transition: all 0.7s ease-in-out;
}
@media (min-width: 768px) {
  .whatsapp_for_wordpress_vi-public-basepop-inner {
    right: 20px;
  }
}
.whatsapp_for_wordpress_vi-public-basepop-inner.opened {
  display: block;
  transition: all 0.7s ease-in-out;
}
.whatsapp_for_wordpress_vi-public-basepop-view-headr {
  display: flex;
  justify-content: flex-start;
  gap: 30px;
  border-bottom: 1px solid #444;
  padding: 10px 20px;
  border-radius: 20px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  align-items: center;
  font-family: "Dosis", sans-serif;
}
.whatsapp_for_wordpress_vi-public-basepop-view-headr-agentimg {
  position: relative;
  max-width: 44px;
}
.whatsapp_for_wordpress_vi-public-basepop-view-headr-agentimg img {
  width: 44px;
  border-radius: 50%;
  border: 1px solid #444;
}
.whatsapp_for_wordpress_vi-public-basepop-view-headr-agentimg:after {
  content: "";
  background-color: #9de62e;
  border: 1px solid;
  width: 10px;
  height: 10px;
  position: absolute;
  right: 7%;
  bottom: 0;
  border-radius: 50%;
}
.whatsapp_for_wordpress_vi-public-basepop-view-headr-agentname {
  font-size: 22px;
}
.whatsapp_for_wordpress_vi-public-basepop-view-body {
  background-repeat: repeat;
  background-size: 100%;
  width: 100%;
  height: 100%;
  max-height: calc(100% - 180px);
  opacity: 0.8;
}
.whatsapp_for_wordpress_vi-public-basepop-view-body-greet {
  padding: 20px 20px 20px 30px;
}
.whatsapp_for_wordpress_vi-public-basepop-view-body-greet-txt {
  background: #fff;
  border-radius: 20px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 7px;
  position: relative;
  border-top-left-radius: 0;
}
.whatsapp_for_wordpress_vi-public-basepop-view-body-greet-txt-agent {
  font-size: 15px;
  font-family: "Dosis", sans-serif;
}
.whatsapp_for_wordpress_vi-public-basepop-view-body-greet-txt p {
  margin: unset;
  font-family: "Dosis", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 600;
}
.whatsapp_for_wordpress_vi-public-basepop-view-body-greet-txt .time {
  font-family: "Dosis", sans-serif;
  display: flex;
  justify-content: flex-end;
  font-size: 14px;
  color: #777;
}
.whatsapp_for_wordpress_vi-public-basepop-view-body-greet-txt::before {
  content: " ";
  position: absolute;
  border-bottom: 18px solid transparent;
  border-right: 18px solid #f7f7f7;
  border-left: none;
  border-right-color: #fff;
  left: -16px;
  position: absolute;
  top: 0;
  z-index: -1;
}
.whatsapp_for_wordpress_vi-public-basepop-view-footr a {
  background: #25d366;
  display: flex;
  margin: 20px;
  border-radius: 15px;
  justify-content: center;
  gap: 10px;
  align-items: center;
  text-decoration: none;
  border: 1px solid transparent;
}
.whatsapp_for_wordpress_vi-public-basepop-view-footr a:hover {
  border-color: #128a49;
}
.whatsapp_for_wordpress_vi-public-basepop-view-footr a img {
  width: 30px;
  height: 30px;
  padding: 5px 0;
}
.whatsapp_for_wordpress_vi-public-basepop-view-footr a span {
  color: #ffffff;
  font-family: "Dosis", sans-serif;
  font-weight: 600;
  letter-spacing: 1px;
}